import * as React from 'react'
import Button from '@mui/material/Button';
import { Container, Grid, Paper, TextField } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatch, RootState } from '../redux/store';
import { authUser } from '../redux/auth/auth.action';


export const LoginScreen: React.FC = () => {
    const [username, setUsername ] = React.useState('')
    const [password, setPassword ] = React.useState('')
    const accessToken = useSelector((state: RootState) => state.auth.data?.token);
    
    const dispatch: AppDispatch = useDispatch();

    const onChangeUsername = (event:any) => {
        setUsername(event.target.value)
    }

    const onChangePassword = (event:any) => {
        setPassword(event.target.value)
    }

    const loginUser = () => {
        dispatch(
            authUser({username: username, password: password}))
            .then( data => console.log(data.payload))
            .then(() =>console.log( "reduxstore token: ",accessToken))
    } 

    return (
        <Container maxWidth="sm">
            <Paper elevation={3} style={{backgroundColor:'#cfe8fc', height: 400, width: 300,display:'flex'}}>
                <Grid>
                    <TextField style={{fontSize:10}} id="standard-basic"  value={username} onChange={onChangeUsername}  label="Matrikelnummer" variant="outlined" size='small'/>
                    <TextField style={{fontSize:10}} id="standard-basic"  value={password} onChange={onChangePassword}  label="Passwort" variant="outlined" size='small'/>
                    <Button color='error' variant="contained" onClick={loginUser}>Login mit JWT</Button>
                </Grid>
            </Paper>
        </Container>
    );
}

