import React from 'react'
import { useEffect } from 'react'
import { useSelector } from 'react-redux'
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'
import { RootState } from '../redux/store'
import { HomeScreen } from "../screens/HomeScreen"
import { LoginScreen } from "../screens/LoginScreen"
import { NewQuizScreen } from "../screens/NewQuizScreen"
import Navbar from "./Navbar"

export {}

export const MainStackNavigator: React.FC = () => {
    const [token, setToken] = React.useState('')
    const accessToken = useSelector((state: RootState) => state.auth.data?.token);
    
    useEffect(() => {
       console.log("Bin hier drin")
       console.log('redux accessToken: ', accessToken)
       const sessionToken = sessionStorage.getItem("token")
       if(sessionToken) setToken(sessionToken)
    })

    return(
        <Router>
        {token?
          <div>
          <Navbar/>
          <Routes>
            <Route path="/" element={<HomeScreen/>} />
            <Route path="/newQuiz" element={<NewQuizScreen/>} />
          </Routes>
        </div>
        :<div>
          <LoginScreen/>
          </div>
        }
      </Router>
    )
}
