import { createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

export const authUser = createAsyncThunk('auth/authUser', async (data:{username:string,password:string}, thunkApi) => {
    try{
        console.log(data.username, data.password)
        const loginuser = await axios
            .post('http://localhost:3001/login', {
                username: data.username,
                password: data.password
            })

        if(loginuser) sessionStorage.setItem("token", loginuser.data.token);
        return loginuser.data.token
        
    }
    catch(error: any){
        console.warn('Error in authUser', error.response)
    }

    return Promise.reject();
})


export const logoutUser = createAsyncThunk('auth/logoutUser', async (_, thunkApi)  => {
    try{
        localStorage.removeItem("user");
    }
    catch(error: any){
        console.warn('Error in logout User', error.response)
    } 
  
  return Promise.reject();
})